
































.app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  text-align: left;
  list-style: none;
  font-size: 20px;
  overflow: auto;
  h3 {
    text-align: center;
    font-size: 40px;
    margin: 50px auto;
  }
  li {
    font-size: 30px;
    margin-bottom: 20px;
    padding: 0 30px;
    line-height: 40px;
  }
}
